.movies .movie {
    width: 300px;
    background-color: white;
    /* margin-bottom: 70px; */
    padding: 20px;
    /* color: #adaeb9; */
    /* border-radius: 6px;  */
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    font-family: system-ui; 
    display: flex;
    justify-content: center; 
}
.movies .movie a {
    /* display: flex; */
    /* align-items: flex-start; */
    /* justify-content: space-between; */
    text-decoration: none;
    color: inherit;
}
.movie img {
    /* position: relative; */
    /* top: -50px; */
    max-width: 250px;
    /* margin-right: 30px; */
    /* box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025); */
    border: 1.5px solid gainsboro;
    border-radius: 5px;
}
.movie .movie__ranking {
    color: white;
    background-color: rgba(0, 0, 0, 0.747);
    width: 27px;
    height: 27px;
    text-align: center;
    padding-top: 4px;
    /* align-content: center; */
    font-size: 15px;
    font-weight: bold;
    border-radius: 4px;
    position: absolute;
    margin: 7px;
}
.movie .movie__year,
.movie .movie__rating,
.movie .movie__book {
    /* margin: 0; */
    font-weight: 350;
    margin: 3.5px 0;
}
.movie .movie__title {
    margin: 3px 0 5px;
    font-weight: 400;
    font-size: 17px;
    color: #1f1f1f;
}
.movie .movie__year {
    font-size: 14px;
    color: #252525;
}
.movie .movie__rating {
    font-size: 14px;
    color: rgb(255, 59, 151);
}
.movie .movie__book {
    font-size: 13px;
    color: #616161;
}
